/* styles.css */

.about-section {
    text-align: center;
    position: relative;
    padding: 60px 0;overflow: hidden;
}
 
  .about-content {
    text-align: left;
    max-width: 800px;
    margin: 0 auto;
    padding: 40px;
    
    transition: transform 0.3s ease;
  }

  .about-span{
    font-size: 20px;
  font-weight: lighter;
  position: absolute;
  
  z-index: 100;
  padding: 10px;
  background-color: #FDE74C;
  border-radius: 10px;
}
  .about-content h2 {
    font-size: 96px;
    font-weight: bold;
    margin-bottom: 20px;
   color: #000;
  }
  @media (max-width: 767px)  {
    
    .about-content h2 {
      font-size: 76px;
      font-weight: bold;
      margin-bottom: 20px;
    }
  }
  
  .about-content p {
    font-size: 20px;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .button{
    position:relative;
    display:inline-block;
    margin:20px;
  }
  
  .button a{
    color:white;
    font-family:Helvetica, sans-serif;
    font-weight:bold;
    font-size:36px;
    text-align: center;
    text-decoration:none;
    background-color:#FFA12B;
    display:block;
    position:relative;
    padding:20px 40px;
    
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    text-shadow: 0px 1px 0px #000;
    filter: dropshadow(color=#000, offx=0px, offy=1px);
    
    -webkit-box-shadow:inset 0 1px 0 #FFE5C4, 0 10px 0 #915100;
    -moz-box-shadow:inset 0 1px 0 #FFE5C4, 0 10px 0 #915100;
    box-shadow:inset 0 1px 0 #FFE5C4, 0 10px 0 #915100;
    
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
  }
  
  .button a:active{
    top:10px;
    background-color:#F78900;
    
    -webkit-box-shadow:inset 0 1px 0 #FFE5C4, inset 0 -3px 0 #915100;
    -moz-box-shadow:inset 0 1px 0 #FFE5C4, inset 0 -3pxpx 0 #915100;
    box-shadow:inset 0 1px 0 #FFE5C4, inset 0 -3px 0 #915100;
  }
  
  .button:after{
    content:"";
    height:100%;
    width:100%;
    padding:4px;
    position: absolute;
    bottom:-15px;
    left:-4px;
    z-index:-1;
    background-color:#2B1800;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
  }


  